import './WhatElse.scss'
import megaDisk from '../../img/pic/megaDisk.webp'
import films from '../../img/pic/films.webp'
import sales from '../../img/pic/sales.webp'
import {connect} from "react-redux";
import showModal from "../../redux/actions/showModal";


function WhatElse(props) {

   const data = [
      {
         "dataView": "megadisk",
         "img": megaDisk,
         "alt": "",
         "title": "1   <br>   ",
         "text": "            .      ."
      },
      {
         "dataView": "mftv",
         "img": films,
         "alt": "",
         "title": "100 000   , <br>    70 -",
         "text": "     .     SMART-,         ."
      },
      {
         "dataView": "vse",
         "img": sales,
         "alt": "",
         "title": "   40% <br>   ",
         "text": " :   10     .    40 % "
      }
   ]


   return (
      <section className="what-else">
         <div className="wrapper">

            <h1>  </h1>

            <div className="what-else__cards">

               {data.map( el => (

                  <div key={el.dataView} className="what-else__card card-what-else">

                     <div className="card-what-else__img">
                        <img src={el.img} alt={el.img}/>
                     </div>

                     <h2 className="card-what-else__title" dangerouslySetInnerHTML={{__html: el.title}}/>
                     {
                        el.dataView !== 'vse'
                           ? <p className="card-what-else__text" dangerouslySetInnerHTML={{__html: el.text}}/>
                           : <p className="card-what-else__text">
                              {el.text} <span className='link'
                                              onClick={() => props.showModal( {modal: 'tariffAll', bool: true} )}></span>.
                           </p>
                     }


                     <span className="card-what-else__link link"
                           onClick={() => props.showModal({modal: 'order', bool: true})}></span>
                  </div>

               ) )}
            </div>
         </div>

      </section>
   )
}

const mapDispatchToProps = {
   showModal
}

export default connect(null, mapDispatchToProps)(WhatElse)